function FA(props){
  return (
    [
      <ellipse cx={props.x+20} cy={props.y+20} rx={18} ry={18} style={{fill:props.color[props.dir%2],stroke:"black",strokeWidth:2}} key={1} />,
      <path d={"M"+(props.x+9)+" "+(props.y+9)+" l19 19 m-15 -5 l15 5 l-5 -15"} stroke="black" strokeWidth="4" fill="none" key={2} />,
      <path d={"M"+(props.x+10)+" "+(props.y+10)+" l18 18 m-14 -4.7 l14 4.7 l-4.7 -14"} stroke="white" strokeWidth="2" fill="none" key={3} />,
      <ellipse cx={props.x+20} cy={props.y+20} rx={18} ry={18} onClick={evt => props.onClick(evt,props.key2)} style={{fill:"white",stroke:"none",opacity:0}} key={4}/>
    ]
  );
}
