function Ar(props){
  let a=[];
  if(props.x!==props.xMAX){
   a.push([      
    <line x1={props.x+41.9} y1={props.y-23.8} x2={props.x+20} y2={props.y+20} key={5} style={{stroke:"black",strokeWidth:9}} />, 
    <line x1={props.x+41.9} y1={props.y-23.8} x2={props.x+20} y2={props.y+20} key={6} style={{stroke:props.color[1],strokeWidth:5}} />]);
  }else{
    a.push([
      <path d={'M'+(props.x+28.1)+' '+(props.y+3.8)+' q21.9 -43.8 0 -87.6'} style={{stroke:"black",strokeWidth:9,fill:"none"}} key={5} />,
      <path d={'M'+(props.x+28.1)+' '+(props.y+3.8)+' q21.9 -43.8 0 -87.6'} style={{stroke:props.color[1],strokeWidth:5,fill:"none"}} key={6} />
    ]);
  }
  if(props.x!==10){
    a.push([<line x1={props.x-1.9} y1={props.y-23.8} x2={props.x+20} y2={props.y+20} key={7} style={{stroke:"black",strokeWidth:9}} />, 
      <line x1={props.x-1.9} y1={props.y-23.8} x2={props.x+20} y2={props.y+20} key={8} style={{stroke:props.color[0],strokeWidth:5}} />
    ]);
  }else{
    a.push([
      <path d={'M'+(props.x+11.9)+' '+(props.y+3.8)+' q-21.9 -43.8 0 -87.6'} style={{stroke:"black",strokeWidth:9,fill:"none"}} key={5} />,
      <path d={'M'+(props.x+11.9)+' '+(props.y+3.8)+' q-21.9 -43.8 0 -87.6'} style={{stroke:props.color[0],strokeWidth:5,fill:"none"}} key={6} />
    ]);
  }
  let b;
  switch(props.dir){
    case 0:
      b=FA(props);
      break;
    case 1:
      b=BA(props);
      break;
    case 2:
      b=FBA(props);
      break;
    case 3:
      b=BFA(props);
      break;
    default:
      b=[];
  }
  return a.concat(b);
}
function Ar2(props){
  return (
    [0,1,2,3,4].map(i=><path d={'M'+(props.x+props.r*i)+' '+(props.y+10)+' l10 -10 l10 10 l-10 10'} style={{fill:props.color,stroke:'black'}} key={i} />)
  )
}
function Co(props){
  /*let a=[]
  for(let i=0;i<props.cs.length;i++){
    a.push(<ellipse cx={i*40+15} cy={15} rx={14} ry={14} style={{fill:props.cs[i],stroke:'black'}} key={i} />)
  }
  return (<svg height={30} width={this.state.color.length*40}>{a}</svg>);*/
  let a=[]
  for(let i=0;i<props.cs.length;i++){
    a.push(<input type="color" className="co" value={props.cs[i]} key={i} />)
  }
  return <div>{a}</div>
}
class Br extends React.Component{
  constructor(props){
    super(props);
    this.state={
      grid:{'0 0':[0,"#FF0000",'#880000'],'1 0':[1,"#00FF00",'#008800'],'0 1':[2,"#FF0000",'#008800']},
      color:["#383838","#0055FF"],
      cPatt:[0,1,0,1],
      strs:2,
      rows:2,
      cP2:'0 1 0 1',
      text2:'0 1\r 2'
    };
    this.addRow();
  }
  handleClick(evt,k){
    const g2=this.state.grid;
    g2[k][0]=((g2[k][0]+1)%4);
    this.setState({grid:g2});
    this.toText()
    this.colorUpdate(g2[k],this.dd(g2[k][0]),this.ksp(k)[1]%2,this.ksp(k));
  }
  ksp(x){
    return x.split(' ').map(i => parseInt(i));
  }
  dd(x){
    let y=0;
    switch(x){
      case 0:
      case 3:
        y=0;
        break;
      default:
        y=-1;
      }
    return y;
  }
  colorUpdate(c,d,d1,k){
    let k2=[d+d1+k[0],1+k[1]];
    const g2=this.state.grid;
    if(!(k2[0]===-1||k2[0]+k2[1]%2>=this.state.strs||k2[1]>=this.state.rows)){
      const e=g2[k2[0]+' '+k2[1]];
      g2[k2[0]+' '+k2[1]][1-d]=c[1+c[0]%2];
    }
    else if(k2[0]===-1&&k2[1]<this.state.rows-1){
      g2['0 '+(k2[1]+1)][1]=c[1+c[0]%2];
    } else if(k2[0]+k2[1]%2===this.state.strs&&k2[1]<this.state.rows-1){
      g2[(k2[0])+' '+(k2[1]+1)][2]=c[1+c[0]%2];
    }
    k2=[-1-d+d1+k[0],1+k[1]];
    if(!(k2[0]===-1||k2[0]+k2[1]%2>=this.state.strs||k2[1]>=this.state.rows)){
      const e1=g2[k2[0]+' '+k2[1]];
      g2[k2[0]+' '+k2[1]][2+d]=c[2-c[0]%2];
      this.setState({grid:g2});
    }
    else if(k2[0]+k2[1]%2===this.state.strs&&k2[1]<this.state.rows-1){
      g2[(k2[0])+' '+(k2[1]+1)][2]=c[2-c[0]%2];
    } else if(k2[0]===-1&&k2[1]<this.state.rows-1){
      g2['0 '+(k2[1]+1)][1]=c[2-c[0]%2];
    }
  }
  renderKnot2(i){
    let i2=this.ksp(i)
    if(i2[1]>=this.state.rows-2) return 0
    const g=this.state.grid[i]
    return <Ar2 x={10*i2[1]} y={20*i2[0]+10*(i2[1]%2)} r={10*(this.state.rows-2)} color={g[1+g[0]%2]} key={i}/>
  }
  renderKnot(i){
    const g=this.state.grid;
    let i2=this.ksp(i)
    return <Ar dir={g[i][0]} x={60*i2[0]+30*(i2[1]%2)+10} xMAX={60*(this.state.strs-1)+10} y={60*i2[1]+20} color={[g[i][1],g[i][2]]} c2={g[i][2]} key={i} onClick={(evt,k) => this.handleClick(evt,k)} key2={i} />
  }
  addKnot(c1,c2,x,r){
    const g2=this.state.grid;
    g2[x+' '+r]=[0,c1,c2];
    this.setState({grid:g2});
  }
  b2c(k1,k2){
    if(k1===-1) return this.b2c(0,k2-1).reverse();
    if(k1+k2%2===this.state.strs) return this.b2c(k1,k2-1).reverse();
    let a=this.state.grid[k1+' '+k2];
    let b=(a[0]-a[0]%2)/2;
    if(k1+k2%2===this.state.strs) return this.b2c(k1,k2-1).reverse()
    return [a[2-b],a[1+b]];
  }
  t2c(i,rs){
    return [this.b2c(i-1+rs%2,rs-1)[1],this.b2c(i+rs%2,rs-1)[0]]
  }
  addRow(){
    for(let j=0;j<2;j++){
      let rs=this.state.rows;
      for(let i=0;i<this.state.strs-rs%2;i++){
        this.addKnot(this.b2c(i-1+rs%2,rs-1)[1],this.b2c(i+rs%2,rs-1)[0],i,rs)
      }
      this.state.rows=rs+1
      this.setState({rows:rs+1});
    }
    this.toText()
  }
  rmvRow(){
    for(let j=0;j<2;j++){
      let rs=this.state.rows-1;
      for(let i=0;i<this.state.strs-rs%2;i++){
        delete this.state.grid[j+' '+rs]
      }
      this.state.rows=rs
      this.setState({rows:rs});
    }
    this.toText()
  }
  addCol(){
    this.state.cPatt.push(0,1)
    this.state.cP2+=' 0 1'
    this.state.strs+=1
    let s=this.state.strs-1
    const g=this.state.grid
    for(let i=0;i<this.state.rows;i++){
      g[(s-i%2)+' '+i]=[0,0,0]
    }
    this.setState({grid:g});
    this.toText()
  }
  rmvCol(){
    let cpt2=this.state.cPatt.slice(0,-2)
    let cp22=this.state.cP2.split(' ').slice(0,-2).join(' ')
    let str2=this.state.strs-1
    let s=this.state.strs-1
    const g=this.state.grid
    for(let i=0;i<this.state.rows;i++){
      delete g[(s-i%2)+' '+i]
    }
    this.setState({grid:g,cPatt:cpt2,cP2:cp22,strs:str2});
    this.toText()
  }
  topColors(evt){
    let i=evt.target.value.split(' ').map(x=>parseInt(x))
    this.setState({cP2:evt.target.value});
    for(let j in i){
      if(isNaN(i[j])) return evt.target.value
    }
    this.setState({cPatt:i})
  }
  colChange(evt,x){
    let c=this.state.color;
    c[x]=evt.target.value;
    this.setState({color:c})
  }
  addColor(){
    let c=this.state.color;
    c.push('#383838')
    this.setState({color:c})
  }
  toText(){
    let s=''
    for(let i=0;i<this.state.rows-2;i++){
      s+=((i%2)===1 ? ' ' : '')
      for(let j=0;j<this.state.strs-i%2;j++){
        s+=this.state.grid[j+' '+i][0]+' '
      }
      s+=(i===this.state.rows-3?'':'\n')
    }
    this.setState({text2:s})
  }
  textChange(evt){
    this.setState({text2:evt.target.value})
    let a=evt.target.value.split('\n')
    let b=a.map(i=>i.split(' ').map(i=>(i===''?'':parseInt(i))))
    for(let i=0;i<b.length;i++){
      let i1=[]
      for(let j=0;j<b[i].length;j++){
        if(b[i][j]==='') i1.push(j)
      }
      i1=i1.reverse()
      for(let j=0;j<i1.length;j++){
        b[i]=b[i].slice(0,i1[j]).concat(b[i].slice(i1[j]+1))
      }
      if(b[i].length===0) b=b.slice(0,i).concat(b.slice(i+1))
    }
    let c=b.length
    let d=b[0].length
    console.log(b)
    if(c%2!=0) return 0
    for(let i=0;i<c;i++){
      if(b[i].length+i%2!=d) return 0
      for(let j=0;j<d-i%2;j++){
        console.log([1,i,j])
        if(isNaN(b[i][j])||b[i][j]<0||b[i][j]>4) return 0
        console.log(2)
      }
    }
    console.log([c,this.state.rows,'aaaa'])
    const gr=this.state.grid
    let ct=0
    for(let i=c;i<this.state.rows-2&&ct<100;i+=2){
      this.rmvRow()
      ct++
      console.log('rl')
    }
    for(let i=c;i>this.state.rows-2&&ct<100;i-=2){
      this.addRow()
      ct++
      console.log([i,'rg',this.state.rows-2])
    }
    for(let i=d;i<this.state.strs&&ct<100;i++){
      this.rmvCol()
      ct++
      console.log('cl')
    }
    for(let i=d;i>this.state.strs&&ct<100;i--){
      this.addCol()
      ct++
      console.log('cg')
    }
    for(let i=0;i<c;i++){
      for(let j=0;j<d-i%2;j++){
        gr[j+' '+i][0]=b[i][j]
      }
    }
    console.log(gr)
    this.setState({grid:gr});
  }
  render(){
    let st=this.state;
    for(let j=0;j<st.strs;j++){
      st.grid[j+' 0'][1]=st.color[st.cPatt[2*j]]
      st.grid[j+' 0'][2]=st.color[st.cPatt[2*j+1]]
    }
    for(let i=1;i<st.rows;i++){
      for(let j=0;j<st.strs-i%2;j++){
        st.grid[j+' '+i][1]=this.b2c(j-1+i%2,i-1)[1]
        st.grid[j+' '+i][2]=this.b2c(j+i%2,i-1)[0]
      }
    }
    let c2=[]
    for(let i=0;i<this.state.color.length;i++){c2.push([i,this.state.color[i]])}
    let eBox=<p>No Errors</p>
    for(let i=0;i<st.strs;i++){
    let a=[st.grid[i+' 0'],st.grid[i+' '+(st.rows-2)]]
      if(![1,2].every(i=>a[0][i]==a[1][i])){
        eBox=<p style={{color:'red'}}>WARNING Top string do not match bottom strings</p>
      }
    }
    return (
      <div>
        <p>Hi! <br />
          <input type="button" value="Add Row" onClick={() => this.addRow()} />
          <input type="button" value="Add Column" onClick={() => this.addCol()} />
          <br />
          <input type="button" value="Remove Row" onClick={() => this.rmvRow()} />
          <input type="button" value="Remove Column" onClick={() => this.rmvCol()} />
        </p>
        {eBox}
        <div>
          {c2.map(i=>[
            <span key={i[1]+'x'}>{i[0]+':'}</span>,
            <input type={"color"} className="co" value={i[1]} key={i[0]} onChange={evt=>this.colChange(evt,i[0])} />]).concat([<input type='button' value='+' onClick={()=>this.addColor()} key='+' />
          ])}
        </div>
        <input type='text' onChange={evt => this.topColors(evt)} value={this.state.cP2}/>
        <div>
          <svg height={20*this.state.strs} width={100*this.state.rows-200}>
            {Object.keys(st.grid).map(i => this.renderKnot2(i))}
          </svg>
        </div>
        <div>
          <svg height={60*this.state.rows-100} width={60*this.state.strs+10}>
            {Object.keys(st.grid).map(i => this.renderKnot(i))}
          </svg>
        </div>
        <div>
          <textarea rows={this.state.rows-2} cols={this.state.strs*2} onChange={evt=>this.textChange(evt)} value={this.state.text2}/>
          <input type='button' value='Reset Text' onClick={()=>this.toText()} />
        </div>
      </div>
    );
  }
}

ReactDOM.render(<Br />, document.getElementById('root'));

serviceWorker.unregister();
